import React, { Component } from 'react';
import {openNew,terms} from './../util/util.js';
import peocock from './../images/peacock.png';
import background from './../images/bg.png';
import { text,locale } from './../util/i18n.js';

const styles ={
	
	featureCard :{
				
				
				fontFamily:'sans-serif',
				//color:'#555',
				fontSize:'15px',			
				float: 'left',
				lineHeight: 1.8,		
				textAlign: 'left',		
				paddingBottom:'50px'
			},
			
			cardDiv : 'w3-round-xxlarge',
			
			selectedStyle :{
				letterSpacing:'1.1px',
				fontSize:'14px',
				fontWeight: 'bold',
			},
			
			vertiStyle :{
				letterSpacing:'1.2px',
				fontSize:'14px',
			},
			
	
	}

//const cards = [];

//const btns = ['WELCOME','KNOW FACTS','REVIEW PRODUCT','OOPS...','TRAIL','UNDERSTAND PRICING', 'BE SATISFIED'];

//const btns = [text('started-welcome'),text('started-knowfacts'),text('started-reviewproduct'),text('started-opps'),text('started-trial'),text('started-understandpricing'),text('started-besatisfied') ];


			
	

let pageno = 0;


export default class GetStarted extends Component{
	
	constructor( props )
   {
       super(props);
	   this.state ={ pagenm : 0,language:null };
	   
   }
   
   render(){
	   if(this.state.language != locale() ){
		   // alert(this.state.language);
			this.loadCards();
		   this.setState({language:locale()});
	   }
	   let padding = this.props.device == 'L' ? '50px' : '1px';
	   
		return(
			
			<div class="w3-cell-row w3-animate-top " style={{maxWidth: '100%',  paddingTop:'40px', paddingLeft:padding, paddingRight:padding }}>
				
				<div>
					<header class="w3-container w3-light-gray w3-center">
					  <h3>Get Started</h3>
					</header>
				<div class='w3-cell w3-hide-small w3-hide-medium w3-border-left ' style = { { width : '15%' } } >	
				
				{ 
					Object.entries( this.btns ).map( ( [ key, value ] ) =>{								
						
							return this.lbtn( key, value, this.state.pagenm );
						}
					)
				}
					
				
				</div>
				
				<div class='w3-cell w3-display-container ' 
				style={{ backgroundImage: "url(" + background + ")", backgroundSize: 'cover', backgroundOrigin: 'border-box', backgroundAttachment: 'fixed',paddingLeft:'5%', height:'500px',paddingRight:'5%',}}   >
					
					<div class ="w3-container w3-cell-bottom w3-hide-small w3-hide-medium" />					
					{ this.cardList( this.state.pagenm ) }					
						
					
					<div className='w3-display-bottommiddle w3-border-top w3-border-blue-gray' style={{fontSize:'14px', width:'90%'}}>	
						
						<button class = "w3-button  w3-left " onClick = { this.prev.bind( this ) }>
							❮❮ PREV
						</button>			
						
						<button class = "w3-button w3-right" onClick = { this.next.bind( this ) } >
							 NEXT ❯❯
						</button>
				
					</div>
				</div>
				<div className=' w3-hide-small w3-center' style={{height:'240px', opacity:'0.5'}}>
					<img src={peocock}/>
				</div>
				
			</div>
			
		</div>
			
	   );
   }
	   
	   componentDidMount(){
		 
		   this.loadCards();
		   this.setState({cardnm:0});
	   }
	   
   
   lbtn = function( idx, lable, pagenm ){
		
		if( idx > this.btns.length-1 ) return ;
		let clz = 'w3-button w3-block    w3-left-align' ;  
		   let style = styles.vertiStyle;
		   
		if( idx == 0  ) {
			clz = ' w3-border-top ' + clz;
		}
		if( idx == pagenm  ) {
			clz = ' w3-border-black w3-border-bottom w3-border-top ' + clz;
			style = styles.selectedStyle;
		}
		else clz = clz + ' w3-border-bottom';
	  
		return (
		
			<div 
				className={clz} 			
				style={style} 
				onClick={() => { this.jmp(idx).bind(this)} }>{lable}
			</div> );
   }
   
   butns = [];
   cards = [];
   cardList = function( pagenm ){
	   
	  
		return (
			<div class=' w3-center w3-round-large w3-left-align '   >
			{this.cards[pagenm]}
				
			</div>
		);
	   
   }
   next = function(){
		
		let vlu = this.state.pagenm+1;
		if( vlu >= this.btns.length ) return;//vlu = btns.length-1;
	
		this.setState({pagenm:vlu});
	}
	prev = function(){
		
		let vlu = this.state.pagenm-1;
		if( vlu < 0 )vlu = 0;
		this.setState({pagenm:vlu});
	}
	jmp = function( idx ){
	
		this.setState( { pagenm:parseInt( idx ) } );
	}
	

	loadCards = function(){
		
		this.btns = [text('started-menu-welcome'),text('started-menu-knowfacts'),text('started-menu-reviewproduct'),text('started-menu-opps'),text('started-menu-trial'),text('started-menu-understandpricing'),text('started-menu-besatisfied') ];
		this.cards = [];
		this.cards.push(
					
			<div class={styles.cardDiv} style={styles.featureCard}>			
				<p >
					<b><li>{text('started-welcome-hdr')}</li></b>
					{text('started-welcome-txt-1')}
					
										
				</p>	
				<p >
					
					{text('started-welcome-txt-2')} <br/><br/> {text('started-welcome-txt-3')}
					
				</p>	
			</div>);
		
		this.cards.push(
				
			<div class={styles.cardDiv} style={styles.featureCard}>			
				<p  >
					<b><li>{text('started-knowfacts-1-hdr')}</li></b>
					
					{text('started-knowfacts-1-txt')}
					
				</p>
				<p  >
					<b><i> - {text('started-knowfacts-11-hdr')} : </i></b>
					{text('started-knowfacts-11-txt')}
					
					
				</p>	
					<p>
						<b><i> - {text('started-knowfacts-12-hdr')} : </i></b>
						{text('started-knowfacts-12-txt')}  
					</p>
				<p>
					<b><li>{text('started-knowfacts-2-hdr')}</li></b>
					{text('started-knowfacts-2-txt')}
				</p>	
				
				
				
				<p >
					<b><li>{text('started-knowfacts-3-hdr')}</li></b>
					* {text('started-knowfacts-31-txt')}<br/>
					* {text('started-knowfacts-32-txt')[0]} 
						<a href='#' onClick={terms}> {text('started-knowfacts-32-txt')[1]}</a>
						{text('started-knowfacts-32-txt')[2]} 
						
				</p>
			</div>
		
			);
	   
		this.cards.push(
				<div class={styles.cardDiv} style={styles.featureCard}>				
					
					
					<p >
						<b><li>{text('started-reviewproduct-1-hdr')}</li></b>	
						{text('started-reviewproduct-1-txt')}
						
					</p>	
						<ul>
						<li>
						
							<i> {text('started-reviewproduct-11-txt')[0]}<a href='#' >{text('started-reviewproduct-11-txt')[1]}</a> {text('started-reviewproduct-11-txt')[2]}<b>{text('started-reviewproduct-11-txt')[3]}</b> {text('started-reviewproduct-11-txt')[4]}	
							</i>
						</li>
						<li>
							<i>{text('started-reviewproduct-12-txt')}</i>
						</li>	
											
						</ul>
					
					<p >
						<b><li>{text('started-reviewproduct-2-hdr')}</li></b>
						{text('started-reviewproduct-2-txt')}
						
						
						<ul><li><i>{text('started-reviewproduct-21-txt')} </i></li></ul>
					</p>
					
					<p >
						<b><li>{text('started-reviewproduct-3-hdr')}</li></b>
						{text('started-reviewproduct-3-txt')}
					</p>
				</div>
			);
			this.cards.push(
				<div class={styles.cardDiv} style={styles.featureCard}>				
					
					
					<p>
						<b><li>Algo trading is not completely legal for retail traders</li></b>	
							Indian exchanges are very cautious while allowing algo-trading, complete machine-driven trading. They are slowly opening up for algo-trading by evaluating the pros and cons of automated trading. Currently, retail traders must have at least one manual check before orders reach exchange.
					</p>
					
					<p >
						<b><li>Delegate review</li></b>
						Even though our trading processes involve human mediations, but trade requests are generated automatically. If you grant us the rights, we can review the order requests generated by Prameyaka on behalf of you.
						<br/><span className="w3-opacity w3-small" >* To adhere with 'PAY AS YOU GAIN', we are <b>NOT</b> charging for order reviews. But We keeps the right to reject the delegation of trade reviews from any customer without giving any reasons.</span><br/>
					</p>					
					
				</div>
			);
			
				
				
			this.cards.push(
				 
				<div class={styles.cardDiv} style={styles.featureCard}>	
					<p >
						<b><li>Open D'Mat account</li></b>
						Currently, we are supporting only <a href='#' onClick={this.zerodha}><b>Zerodha</b></a>, the largest stockbroker in India. If you don't already have an account with them, then you need to open a trading / Demat account. We can assist you in opening the account. Brokers usually charge for opening and maintaining the account.
					</p>
					<p >
					<b><li>Start with TRAIL </li></b>
					Prameyaka offerings are free for a month, but you should be aware of the obvious fact that you need to bear in case of loss.					</p>
					<p ><i>
						 * Click on <a href='#' >Login</a> from the main menu. After selecting your broker, sign-in using credentials provided by your broker.</i>
					</p>
					<p>
						Prameyaka Server restarts at 7 AM IST cleaning all trade containers. You need to log-in after 7 AM, preferably before the market starts with credentials provided by your broker. Once your broker validates your credential, your trade container will be created which can be activated by authorizing prameyaka for managing your trades.
						
						Make sure you cautiously allocate funds that you can try with Prameyaka else all your available funds will be used.
						
					</p>
					
					<p >
						<b><li>Start with a small amount</li></b>
						Stock Market trading is exciting and highly tempting, daily returns are highly volatile. You start with a small amount which can be increased with a portion of the profit you earned.
						But, You should have a minimum of Rs. 5000 available in the trading account to use our product.
					</p>						
				</div>
			);

			
			this.cards.push(
				<div class={styles.cardDiv} style={styles.featureCard}>	
					
					<p >
					<b><li>Pay As You Gain</li></b>
						All the offerings are free if you don't earn more than a decent profit, equivalent to an annual return of 12%. Otherwise monthly charge will be <b>25%</b> of net profit with exemption of 1% *. Taxes and brokerage are external charges which has been deducted before calculating your net profit. This fee structure won't mandate users to log in and authorize each day.
						<br/><br/>
						If your earn monthly return of 10%, equivalent to 120% of annual return then you need to pay Rs. 2475 for 1 lacks of average fund allocation. Similarly monthly charge will be RS 9750 for 40%(eq. annual 480% ),  Rs. 4950 for 20%( eq. annual 240% ), Rs. 1237.5 for 5% ( eq. annual 60% ), and Rs 0 for less than 1%( eq. annual 12% ). <br/>In profitable scenarios, after paying our charges your return will be a bit more than 3 times our charges.
						
						<br/><span className="w3-opacity w3-small" >* Unless otherwise indicated, our charges include payment gateway charges and taxes as per taxation policy, Government of India.  </span><br/>
					</p>
					<p>
					<b><li>Subscribe</li></b>
						If you are satisfied with trial and our fee structure, then you need to subscribe by paying for first month's gain. Your one month free period will be moved to next month.
					</p>
				</div>	
			);
				
			this.cards.push(
				<div class={styles.cardDiv} style={styles.featureCard}>	
					
					<p >
				<b><li>Be Satisfied</li></b>
				We try to keep our customers satisfied, but don't try to keep un-satisfied customers. We don't like to be the reason for our customer's loss. Any time If you are not satisfied, stop your trade container (trading hours) and stop authorizing Prameyaka from the next day. Your last month's charges will be covered under the One Month Free offer. If you change your mind, only then you need to pay any outstanding amount for subscription renewal.			</p>
			
			<p >
				<b><li>Feedback</li></b>
				We thankfully receive any feedback or suggestions and do a thorough feasibility analysis. But complaints will be received only if there are any defects in our platform or discrepancy in monthly charges. 
				Any complaints should be raised with-in 30 days, else we may reject the complaint.
			</p>
				</div>	
			);
				
			
	}
   zerodha = function(){
	   openNew('https://zerodha.com');
   }
}


